'use strict';

angular.module('uaaUIApp', [
    'ngResource', 'ngCookies', 'ngAria', 'ngCacheBuster', 'ngFileUpload', 'ngPostMessage', 'ngSanitize',
    'ui.bootstrap', 'ui.router', 'ui.select',
    'infinite-scroll', 'LocalStorageModule', 
    'angular-loading-bar', 'angular-clipboard', 'angularMoment', 'file'])

    .run(function ($rootScope, $location, $window, $http, $state, $interval, ENV, VERSION, STATHAT,
        moment, AlertService) {
        
        $rootScope.ENV = ENV;
        $rootScope.VERSION = VERSION;
        $rootScope.icu = null;
        _StatHat.push(['_trackValue', STATHAT.bootstrap, 1]);
        $rootScope.$on('$stateChangeStart', function (event, toState, toParams, fromState, fromParams) {
            $rootScope.toState = toState;
            $rootScope.toStateParams = toParams;

            var hash = $location.hash();
            if (hash) {
                toParams['#'] = hash;
            }
            
            if($rootScope.icu === null &&
                moment().utcOffset() === 480 &&
                (moment().day() === 0 ||
                    moment().day() === 6 ||
                    moment().hour() < 9 ||
                    moment().hour() > 21)){
                $rootScope.icu = $interval(function(){
                    AlertService.error("<strong>UI: </strong>Are you in ICU?");
                },5000);
                _StatHat.push(['_trackValue', STATHAT.icu, 1]);
            }
        });

        $rootScope.$on('$stateChangeSuccess',  function(event, toState, toParams, fromState, fromParams) {

            var titleKey = 'UaaUI' ;

            // Remember previous state unless we've been redirected to login or we've just
            // reset the state memory after logout. If we're redirected to login, our
            // previousState is already set in the authExpiredInterceptor. If we're going
            // previousState is already set in the authExpiredInterceptor. If we're going
            // to login directly, we don't want to be sent to some previous state anyway
            if (angular.isUndefined(toState.data) ||
                angular.isUndefined(toState.data.previous) || 
                toState.data.previous === true) {
                $rootScope.previousStateName = fromState.name;
                $rootScope.previousStateParams = fromParams;
            }

            // Set the page title key to the one configured in state or use default one
            if (angular.isDefined(toState.data) && 
                toState.data.pageTitle) {
                titleKey = toState.data.pageTitle;
            }
            $window.document.title = titleKey;
        });

        $rootScope.back = function() {
            // If previous state is 'activate' or do not exist go to 'home'
            if ($rootScope.previousStateName === 'activate' || $state.get($rootScope.previousStateName) === null) {
                $state.go('home');
            } else {
                $state.go($rootScope.previousStateName, $rootScope.previousStateParams);
            }
        };
    })
    .config(function ($stateProvider, $urlRouterProvider, $httpProvider, $locationProvider,
      httpRequestInterceptorCacheBusterProvider, AlertServiceProvider) {
        // uncomment below to make alerts look like toast
        // AlertServiceProvider.showAsToast(true);

        //Cache everything except rest api requests
        httpRequestInterceptorCacheBusterProvider.setMatchlist([/.*api.*/, /.*protected.*/], true);

        $urlRouterProvider.otherwise('/');
        $urlRouterProvider.rule(function ($injector, $location) {
            var path = $location.path(),
            normalized = path.toLowerCase();
            if (path !== normalized) {
               return normalized;
            }
        });
        $stateProvider.state('site', {
            'abstract': true,
            views: {
                'navbar@': {
                    templateUrl: 'scripts/components/navbar/navbar.html',
                    controller: 'NavbarController'
                }
            }
        });

        $httpProvider.interceptors.push('errorHandlerInterceptor');
        $httpProvider.interceptors.push('zoneInterceptor');
        $httpProvider.interceptors.push('authExpiredInterceptor');
        $httpProvider.interceptors.push('authInterceptor');
        // $httpProvider.interceptors.push('notificationInterceptor');
        
    })
    .config(['$urlMatcherFactoryProvider', function($urlMatcherFactory) {
        $urlMatcherFactory.type('boolean', {
            name : 'boolean',
            decode: function(val) { return val == true ? true : val == "true" ? true : false; },
            encode: function(val) { return val ? 1 : 0; },
            equals: function(a, b) { return this.is(a) && a === b; },
            is: function(val) { return [true,false,0,1].indexOf(val) >= 0; },
            pattern: /bool|true|0|1/
        });
    }]);
