'use strict';

angular.module('uaaUIApp')
    .factory('Setting', function apiService(localStorageService, SETTING) {
        return {
            version: 7,
            set: function(setting) {
                localStorageService.set('setting', setting);
            },
            get: function () {
                var setting = localStorageService.get('setting');
                if(setting === null || setting.version < this.version){
                    setting = angular.merge({},SETTING);
                    localStorageService.set('setting', setting);
                }
                return setting;
            }
        };
    });

'use strict';

angular.module('uaaUIApp')
    .factory('Principal', function Principal($q, $filter, UserInfo) {
        var _identity,
            _authenticated = false,
            _token,
            _zone,
            _uaaLogin = false;

        var that = {
            isIdentityResolved: function () {
                return angular.isDefined(_identity);
            },
            isAuthenticated: function () {
                return _authenticated;
            },
            isUAALogin: function(){
                return _uaaLogin;
            },
            isClient: function(){
                return angular.isDefined(_token) &&
                    _token.grant_type === 'client_credentials';
            },
            isRedirect: function(){
                return angular.isDefined(_token) &&
                    (_token.grant_type === 'authorization_code' ||
                    _token.grant_type === 'implicit');
            },
            //Using uaa zone client to manage other zone
            isSwitchingZone: function(){
                if (!_token || !_zone) {
                    return false;
                }
                return _token.zid !== _zone.id &&
                    that.canSwitchingZone(_zone.id);
            },
            canSwitchingZone: function(zoneId){
                if (!_authenticated || !_token) {
                    return false;
                }
                if(zoneId === 'uaa'){
                    return false;
                }
                var find = $filter('filter')(_token.scope, 'zones.' + zoneId + '.');
                return find.length > 0;
            },
            userName: function() {
                return that.identity().then(function(_id) {
                    if(_id == null){
                        return 'UNKNOW';
                    }
                    return _id.user_name;
                }, function(err){
                    return 'UNKNOW';
                });
            },
            hasScope: function (scope) {
                if (!_authenticated || !_token) {
                    return false;
                }
                if (that.isSwitchingZone()) {
                    scope = 'zones.' + _token.zid + '.' + scope;
                }
                return _token.scope.indexOf(scope);
            },
            hasAnyScope: function (scopes) {
                if (!_authenticated || !_token) {
                    return false;
                }
                if (that.isSwitchingZone()) {
                    scopes = 'zones.' + _token.zid + '.' + scopes;
                }

                for (var i = 0; i < scopes.length; i++) {
                    if (_token.scope.indexOf(scopes[i]) !== -1) {
                        return true;
                    }
                }

                return false;
            },
            hasAuthority: function (authority) {
                if (!_authenticated || !_token) {
                    return false;
                }
                if (that.isSwitchingZone()) {
                    authority = 'zones.' + _token.zid + '.' + authority;
                }

                return _token.aud.indexOf(authority);
            },
            hasAnyAuthority: function (authorities) {
                if (!_authenticated || !_token) {
                    return false;
                }
                if (that.isSwitchingZone()) {
                    authorities = 'zones.' + _token.zid + '.' + authorities;
                }

                for (var i = 0; i < authorities.length; i++) {
                    if (_token.aud.indexOf(authorities[i]) !== -1) {
                        return true;
                    }
                }

                return false;
            },
            uaaLogin: function(flag){
                _uaaLogin = flag;
            },
            token: function(token){
                _token = token;
                if(that.isRedirect()){
                    _uaaLogin = true;
                }

                if(that.isClient()){
                    that.authenticate({
                        user_name: token.client_id
                    });
                }else{
                    that.userName();
                }
                return that;
            },
            zone: function(zone){
                _zone = zone;
                return that;
            },
            authenticate: function (identity) {
                _identity = identity;
                _authenticated = typeof identity !== 'undefined';
            },
            identity: function (force) {
                var deferred = $q.defer();

                if (force === true) {
                    _identity = undefined;
                }

                // check and see if we have retrieved the identity data from the server.
                // if we have, reuse it by immediately resolving
                if (angular.isDefined(_identity)) {
                    deferred.resolve(_identity);

                    return deferred.promise;
                }

                // retrieve the identity data from the server, update the identity object, and then resolve.
                UserInfo.get().$promise
                    .then(function (info) {
                        if (angular.isUndefined(info.user_name)) {
                            _identity = undefined;
                            _authenticated = false;
                            deferred.reject(null);
                            return;
                        }
                        _identity = info;
                        _authenticated = true;
                        deferred.resolve(_identity);
                    })
                    .catch(function() {
                        _identity = undefined;
                        _authenticated = false;
                        deferred.reject(null);
                    });
                return deferred.promise;
            }
        };

        return that;
    });

'use strict';

angular.module('uaaUIApp')
    .factory('TokenHolder', function apiService(localStorageService,$window) {
        return {
            set: function(token) {
                var access_token = token.access_token ? token.access_token : null;
                if(access_token) {
                    console.log('JWT:', access_token);
                    
                    var expiredAt = new Date();
                    expiredAt.setSeconds(expiredAt.getSeconds() + token.expires_in);
                    token.expires_at = expiredAt.getTime();
                    
                    localStorageService.set('token', token);
                }
            },
            get: function () {
                return localStorageService.get('token');
            },
            remove: function () {
                return localStorageService.remove('token');
            },
            parseJwt: function(token) {
                var base64Url = token.split('.')[1];
                var base64 = base64Url.replace('-', '+').replace('_', '/');
                return angular.fromJson($window.atob(base64));
            },
            hasValidToken: function () {
                var token = this.get();
                return token && token.expires_at && token.expires_at > new Date().getTime();
            },
            getJwt: function(){
                var token = this.get();
                if(token === null){
                    return {
                        header: undefined,
                        payload: undefined,
                        signature: undefined
                    };
                }
                var jwt = token.access_token.split('.');
                
                //Base64URL to Base64
                var header = jwt[0].replace(/-/g, '+').replace(/_/g, '/');
                var payload = jwt[1].replace(/-/g, '+').replace(/_/g, '/');
                var signature = jwt[2].replace(/-/g, '+').replace(/_/g, '/');
                header = angular.fromJson($window.atob(header));
                var json = $window.atob(payload);
                json = decodeURIComponent(escape(json))
                payload = angular.fromJson(json);
                signature = $window.atob(signature);
                return {
                    header: header,
                    payload: payload,
                    signature: signature
                };
            },
            isSupportRefresh: function(){
                var token = localStorageService.get('token');
                return token !== null && 
                    angular.isDefined(token.refresh_token);
            }
        };
    });

'use strict';

angular.module('uaaUIApp')
    .factory('ZoneHolder', function (Zone, localStorageService, $q) {
        var _current;
        var _uaa = true;

        restoreZone();

        function restoreZone(){
            _current = localStorageService.get("zone");
            _uaa = (_current === null || _current.id === 'uaa');
        }
        function is_uaa(){
            return _uaa;
        }
        function now(){
            var deferred = $q.defer();

            if(_current === null){
                changeZone('uaa')
                    .then(function(zone){
                        deferred.resolve(zone);
                    });
            }else{
                deferred.resolve(_current);
            }

            return deferred.promise;
        }
        function nowName(){
            var deferred = $q.defer();
            now().then(function(zone){
                deferred.resolve(zone.name);
            });
            return deferred.promise;
        }
        function changeZone(id){
            if(_current !== null && 
                _current.id === id){
                var deferred = $q.defer();
                deferred.resolve(_current);
                return deferred.promise;
            }else{
                return Zone.get({id: id}).$promise
                    .then(function(zone){
                        localStorageService.set("zone",zone);
                        _uaa = (zone.id === 'uaa');
                        _current = zone;
                        return zone;
                    });
            }
        }
        function resetZone(){
            localStorageService.remove("zone");
            _uaa = true;
            _current = null;
        }

        return {
            isUAA: is_uaa,
            current: now,
            name: nowName,
            change: changeZone,
            reset: resetZone
        };
    });


'use strict';

angular.module('uaaUIApp')
    .factory('Search', function apiService($filter, GuessSearch) {

        var _load;
        var _selected;
        var _filters;
        var _anys;

        var getAfterMomentName = function(context){
            return 'After ' + context.moment.fromNow();
        };

        var getBeforeMomentName = function(context){
            return 'Before ' + context.moment.fromNow();
        };

        var getISOMoment = function(context){
            return context.moment.toISOString();
        };
        
        var getTextMoment = function(context){
            return context.text;
        };
        
        var getText = function(context){
            return context;
        };

        var getAscSortName = function(context){
            return 'Sort ' + context + ' Asc';
        };

        var getDescSortName = function(context){
            return 'Sort ' + context + ' Desc';
        };

        return {
            // attribute {
            //     name,field,moment,order,any
            // }
            init: function(load, selected, attributes, configs) {
                _load = load;
                _selected = selected;
                _anys = $filter('filter')(attributes, {'any':true}, true);
                var _moments = $filter('filter')(attributes, {'moment':'!!'});
                var _urls = $filter('filter')(attributes, {'url':true}, true);
                var _emails = $filter('filter')(attributes, {'email':true}, true);
                var _enums = $filter('filter')(attributes, function(value, index, array){
                    return angular.isArray(value.enum);
                }, true);
                var _sorts = $filter('filter')(attributes, {'sort':true}, true);


                var _configs = [];
                //all
                _configs.push({
                    support: 'string',
                    data: {
                        group: 'Search',
                        icon: 'glyphicon-search',
                        name: function(context){
                            return 'ANY:' + context;
                        },
                        field: "ANY",
                        operator: "co",
                        value: function(context){
                            return context;
                        }
                    }
                });

                //moment
                if(_moments.length > 0){
                    var _moment_datas = [];
                    angular.forEach(_moments,function(attribute){
                        var after = {
                            group: attribute.name,
                            icon: "glyphicon-time",
                            name: getAfterMomentName,
                            tags: getTextMoment,
                            field: attribute.field,
                            operator: 'ge',
                            value: getISOMoment
                        };
                        _moment_datas.push(after);

                        var before = {
                            group: attribute.name,
                            icon: "glyphicon-time",
                            name: getBeforeMomentName,
                            tags: getTextMoment,
                            field: attribute.field,
                            operator: 'le',
                            value: getISOMoment
                        };
                        _moment_datas.push(before);
                        if(angular.isDefined(attribute.value)){
                            after.value = attribute.value;
                            before.value = attribute.value;
                        }
                    });
                    _configs.push({
                        support: 'moment',
                        data: _moment_datas
                    });
                }

                //url
                if(_urls.length > 0){
                    var _url_datas = [];
                    angular.forEach(_urls,function(attribute){
                        var item = {
                            group: attribute.name,
                            icon: "glyphicon-retweet",
                            name: getText,
                            tags: getText,
                            field: attribute.field,
                            operator: 'ge',
                            value: getText
                        };
                        _url_datas.push(item);
                    });
                    _configs.push({
                        support: 'url',
                        data: _url_datas
                    });
                }

                //_emails
                if(_emails.length > 0){
                    var _email_datas = [];
                    angular.forEach(_emails,function(attribute){
                        var item = {
                            group: attribute.name,
                            icon: "glyphicon-envelope",
                            name: getText,
                            tags: getText,
                            field: attribute.field,
                            operator: 'co',
                            value: getText
                        };
                        _email_datas.push(item);
                    });
                    _configs.push({
                        support: 'email',
                        data: _email_datas
                    });
                }

                //enum
                if(_enums.length > 0){
                    var _enum_datas = [];
                    angular.forEach(_enums,function(attribute){
                        var a = {
                            group: attribute.name,
                            field: attribute.field,
                        };
                        angular.forEach(attribute.enum, function(e){
                            var data = angular.merge({},a,e);
                            _enum_datas.push(data);
                        });
                        
                        // angular.merge(_enum_datas,attribute.enum);
                    });
                    _configs.push({
                        support: true,
                        data: _enum_datas
                    });
                }

                //
                if(angular.isDefined(configs)){
                    _configs.concat(configs);
                }
                
                //sort 
                if(_sorts.length > 0){
                    var _sort_datas = [];
                    angular.forEach(_sorts,function(attribute){
                        var ascending = {
                            group: attribute.name,
                            icon: "glyphicon-sort-by-attributes",
                            name: getAscSortName(attribute.name),
                            field: attribute.field,
                            operator: 'ORDER',
                            value: 'ascending'
                        };
                        _sort_datas.push(ascending);

                        var descending = {
                            group: attribute.name,
                            icon: "glyphicon-sort-by-attributes-alt",
                            name: getDescSortName(attribute.name),
                            field: attribute.field,
                            operator: 'ORDER',
                            value: 'descending'
                        };
                        _sort_datas.push(descending);
                    });
                    _configs.push({
                        support: function(search,selected){
                            if(angular.isDefined(selected)){
                                var already_sort = $filter('filter')(selected, {'operator':'ORDER'}, true);
                                return (already_sort.length === 0);
                            }
                            return true;
                        },
                        data: _sort_datas
                    });
                }
                
                //other
                _configs.push({
                    support: true,
                    data: [{
                        group: 'Advanced',
                        icon: 'glyphicon-random',
                        tags: 'exact and',
                        name: 'EXACT',
                        field: "EXACT",
                        operator: "",
                        value: "with 'AND' operator"
                    }]
                });

                GuessSearch.config(_configs);

                _filters = GuessSearch.guess();
                return _filters;
            },
            tagging: function (text) {
                var filters = GuessSearch.guess(text,_selected.value);
                _filters.length = 0;
                angular.merge(_filters,filters);
                return {
                    group: 'Search',
                    icon: 'glyphicon-search',
                    name: 'ANY:' + text,
                    field: "ANY",
                    operator: "co",
                    value: text
                };
            },
            filtering: function(selected){
                var filters = [];
                var find = $filter('filter')(selected, {'field':'EXACT'}, true);
                var and = (find.length > 0);
    
                angular.forEach(selected,function(select){
                    var filter = '';
                    if(select.field === 'EXACT' || 
                        select.operator === 'ORDER'){
                        return;
                    }else if(select.field === 'ANY'){
                        var ps = [];
                        angular.forEach(_anys, function(attribute){
                            var p = attribute.field + ' co \'' + select.value + '\'';
                            ps.push(p);
                        });

                        filter = 
                            (and ? '(' : '') +
                            ps.join(' or ') + 
                            (and ? ')' : '');
                    }else{
                        filter = 
                            select.field + ' ' +
                            select.operator + ' ' + 
                            (angular.isString(select.value) ? '\'' + select.value + '\'' :  select.value);
                    }
                    filters.push(filter);
                });
    
                var filter = filters.join(and ? ' and ' : ' or ');
                
                var sortBy = null;
                var sortOrder = null;
                var orders = $filter('filter')(selected, {'operator':'ORDER'}, true);
                if(orders.length > 0){
                    sortBy = orders[0].field;
                    sortOrder = orders[0].value;
                }
    
    
                _load(filter,sortBy,sortOrder);
                this.tagging();
            },
            refreshing: function(){
                this.filtering(_selected.value);
            },
            searching: function(text){
                if(!!text){
                    _selected.value = [{
                        group: 'Search',
                        icon: 'glyphicon-search',
                        name: 'ANY:' + text,
                        field: "ANY",
                        operator: "co",
                        value: text
                    }];
                }
                this.refreshing();
            },
            usingText: getText
        };
    });

